import React, { useContext } from 'react';

import Box from '../../components/Box'
import Text from '../../components/Text'

import fundInfoContext from '../../contexts/fundInfo/context'

const scrollToFundData = () => {
  document.getElementById('fund-data').scrollIntoView({ behavior: 'smooth' });
}

const sections =  [
  {
    title: '基金介紹',
    onClick: () => {
      document.getElementById('fund-head').scrollIntoView({ behavior: 'smooth' });
    }
  },
  {
    title: '資產配置',
    onClick: scrollToFundData,
  },
  {
    title: '淨值走勢',
    onClick: scrollToFundData,
  },
  {
    title: '配息紀錄',
    onClick: scrollToFundData,
  },
]

const Sideinfo = () => {
  const { setActiveTab } = useContext(fundInfoContext)
  return (
    <Box
      position="fixed"
      top="60%"
      transform="translate(0, -50%)"
      right="0"
      width="2.5em"
      bg="white"
      zOrder={5}
    >
      {sections.map((s, i) => (
        <Box
          key={i}
          textAlign="center"
          px="0.5em"
          py="1em"
          onClick={() => {
            if (i) {
              setActiveTab(i - 1)
            }
            setTimeout(s.onClick)
          }}
          border="1px solid"
          bg="primary"
          color="white"
        >
          <Text>{s.title}</Text>
        </Box>
      ))}
    </Box>
  );
};

export default Sideinfo;
