import React, { useEffect } from 'react';
import stickybits from 'stickybits'

import Box from './Box'

const Sticky = ({
  children,
  useStickyClasses,
  verticalPosition,
  scrollEl,
  stickyBitStickyOffset,
  noStyles,
  parentClass,
  stickyClass,
  stuckClass,
  useFixed,
  useGetBoundingClientRect,
}) => {
  useEffect(() => {
    if (window.stickybitsInstance) {
      window.stickybitsInstance.update({
        useStickyClasses,
        verticalPosition,
        scrollEl,
        stickyBitStickyOffset,
        noStyles,
        parentClass,
        stickyClass,
        stuckClass,
        useFixed,
        useGetBoundingClientRect,
      })
    } else {
      window.stickybitsInstance = stickybits('.stickybits', {
        useStickyClasses,
        verticalPosition,
        scrollEl,
        stickyBitStickyOffset,
        noStyles,
        parentClass,
        stickyClass,
        stuckClass,
        useFixed,
        useGetBoundingClientRect,
      });
    }

    return function cleanup() {
      window.stickybitsInstance.cleanup()
      delete window.stickybitsInstance
    }
  }, [])
  return (
    <Box className="stickybits" zOrder={2}>
      {children}
    </Box>
  );
};

export default Sticky;
